import React from "react"
import { formatNumber } from "@rp/common/utils"
import { useResultsPageState } from "@rp/react/web/common/providers"
import { useCurrentCQSPreview } from "@rp/react/web/common/providers"
import { Typography } from "@rp/react/web/mui/core"

const ResultsCount = () => {
  const [resultsPageState] = useResultsPageState()
  const { isLoading: isLoadingPreview, isFetching, documentCount } = useCurrentCQSPreview()

  return (
    <Typography
      variant="caption"
      sx={{
        color: "grey.300",
      }}
    >
      About{" "}
      {isLoadingPreview || (isFetching && resultsPageState === "loading")
        ? "..."
        : formatNumber(documentCount)}{" "}
      result
      {documentCount === 1 ? null : "s"}
    </Typography>
  )
}

export { ResultsCount }
